.section {
    width: 100%;
    margin-top: 50px;

    >.container {
        width: 80%;

        >h1 {
            font-size: 30px;
            line-height: 28px;
            font-weight: 600;
            font-family: 'Raleway', sans-serif;
        }

        >p {
            font-size: 14px;
            line-height: 26px;
            font-family: 'Roboto', sans-serif;
            font-weight: 500;
            >ul{
                >li{
                    >h4{
                        font-size: 18px !important;
                        width: 100%;
                        font-size: 25px;
                        line-height: 28px;
                        font-weight: 600;
                        font-family: 'Raleway', sans-serif;
                        margin-bottom: 10px;
                    }

                }
            }
        }
        
        >h3{
            font-size: 20px !important;
            width: 100%;
            font-size: 25px;
            line-height: 28px;
            font-weight: 600;
            font-family: 'Raleway', sans-serif;
            margin-bottom: 10px;
        }


    }

    >.service-box {
        >div {
            >div {
                width: 80%;
                margin-bottom: 30px;
                >.box1 {
                    width: 100%;
                    margin-left: 0;
                    >h1 {
                        text-align: left;
                        width: 100%;
                        font-size: 25px;
                        line-height: 28px;
                        font-weight: 600;
                        font-family: 'Raleway', sans-serif;
                        margin-bottom: 10px;
                    }

                    >.text {
                        >h3 {
                            font-size: 20px;
                            line-height: 28px;
                            font-weight: 600;
                            font-family: 'Raleway', sans-serif;
                        }

                        >p {
                            font-size: 14px;
                            line-height: 26px;
                            font-family: 'Roboto', sans-serif;
                            font-weight: 500;
                            >button{
                                display: block;
                            }
                        }
                    }

                }
            }
        }
    }
}