@keyframes imgUp {
    to {
        transform: translateY(-3px);
    }
}


header {
    display: flex;
    flex-direction: column;
    font-family: 'Raleway', sans-serif;

    >.contact {
        width: 100%;
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;

        >ol::before {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            top: 0px;
            right: 10px;
            background-color: #31096e;
            transform: skewX(25deg);


        }

        >ol {
            width: 50%;
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            background-color: #31096e;
            list-style: none;
            margin: 0%;
            font-size: 12px;
            font-family: 'Roboto', sans-serif;
            font-weight: 500;

            >li {
                display: flex;
                align-items: center;
                justify-content: center;
                align-items: center;
                justify-content: center;
                z-index: 9999;
                padding: 2px 5px;
                cursor: pointer;
                font-size: 12px;
                margin-right: 8px;
                svg {
                    padding-top: 5px;
                    font-size: 1.5rem;
                    color: white;
                }
            }
            >h4{
                display: flex;
                align-items: center;
                justify-content: center;
                align-items: center;
                justify-content: center;
                z-index: 9999;
                padding-top: 8px;
                cursor: pointer;
                font-size: 12px;
                margin-right: 8px;
                >div {
                    display: flex;

                    >div {
                        padding: 0px 10px;
                        animation: imgUp 1.2s linear infinite alternate;
                        transition: all 0.5s;
                        svg {
                            padding-top: 5px;
                            font-size: 1.5rem;
                            color: white;
                        }
                   
                    }

                }
            }
        }


    }
}

@media only screen and (max-width: 992px) {
    header {
        >.contact {
            >ol::before {
                content: none;
            }

            >ol {
                width: 100%;
            }
        }
    }
}

@media only screen and (max-width: 480px) {
    header {
        >.contact {
            >ol::before {
                content: none;
            }

            >ol {
                width: 100%;
                >li{
                    display: flex;
                    flex-direction: column;
                    margin-right: 0px !important;
                    padding: 2px !important;
                }
                >h4 {
                    display: flex;
                    flex-direction: column;
                    
                    margin-right: 0px !important;
                    padding: 8px 0px 0px 0px !important;
                }
            }
        }
    }
}

@media only screen and (max-width: 320px) {
    header {
        >.contact {
            >ol {
                >h4 {
                    display: none;
                }
            }
        }
    }
}