body {
    padding-right: 0;
}

header {
    width: 100%;
    box-shadow: 0 3px 8px rgba(0, 0, 0, 25%);
    display: flex;
    flex-direction: column;
    z-index: 9999;
    background-color: white;

    >.container-fluid {
        width: 100%;
        padding: 0 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 80px;

        >.nav-logo {
            display: flex;
            width: 30%;
            text-align: center;
            height: 80px;

            >a {
                >img {
                    position: absolute;
                    width: 230px;
                    height: 220px;
                    top: -38px;
                    left: 50px;
                }

            }


            >h1 {
                font-size: 16px;
                margin-top: 50px;
                font-family: 'Roboto', sans-serif;
                font-weight: 900;
                cursor: pointer;
                z-index: 999;

                >a {
                    color: black;
                    cursor: pointer;
                    text-decoration: none;
                }
            }
        }

        >.navitems {
            width: 70%;
            align-items: center;
            justify-content: flex-end;
            letter-spacing: 3px;

            //SERVICES
            >.right-menu {
                padding: 0 5px;

                &:hover {
                    >.dropdown-menu {
                        display: block;
                    }
                }

                //FIRST DROPDOWN
                >.menu-button {

                    border: none;
                    background-color: transparent;
                    cursor: pointer;
                    font-weight: 580;
                    font-size: 13px;
                    letter-spacing: 1px;
                    text-align: center;
                    margin-inline: 3%;

                    >.active {
                        color: #DC152A;
                    }

                    >a {
                        display: flex;
                        text-decoration: none;
                        color: black;
                        font-family: 'Roboto', sans-serif;
                        transition: all 0.3s ease-in;
                        padding-top: 10px;

                        &:hover {
                            color: #DC152A;
                            padding: 0 15px;
                        }

                        >svg {
                            padding-bottom: 5px;
                        }
                    }
                }

                >.dropdown-menu {
                    display: none;
                    position: absolute;
                    right: 20%;
                    z-index: 1;
                    border-radius: 0;
                    box-shadow: 4px 5px 10px -5px black;

                    >.grid-container {
                        width: 230px;

                        //SECOND DROPDOWN
                        .grid-item {
                            padding: 10px 0px 10px 10px;
                            text-align: center;
                            border-bottom: 1px solid #73738c;
                            cursor: pointer;
                            font-weight: 600;
                            font-size: 11px;
                            color: #73738c;

                            &:hover {
                                >.dropdown-submenu {
                                    display: block;
                                }
                            }

                            >.submenu-button {
                                justify-content: flex-start;
                                padding: 5px 0 0 0;
                                border: none;
                                background-color: transparent;
                                cursor: pointer;
                                font-weight: 500;
                                font-size: 11px;
                                letter-spacing: 3px;
                                text-align: center;

                                >.active {
                                    color: black;
                                }

                                >.notneeded {
                                    cursor: no-drop;
                                    color: rgb(117, 115, 115);

                                    >svg {
                                        font-size: 13px;
                                    }
                                }


                                >a {
                                    display: flex;
                                    text-decoration: none;
                                    font-family: 'Roboto', sans-serif;
                                    color: rgb(117, 115, 115);
                                    transition: all 0.3s ease-in;

                                    &:hover {
                                        color: black;
                                    }

                                    >svg {
                                        font-size: 13px;
                                    }
                                }
                            }

                            >.dropdown-submenu {
                                margin-top: -26px;
                                left: 100%;
                                display: none;
                                position: absolute;
                                border-radius: 0;
                                padding: 0;
                                border: none;
                                text-align: left;
                                width: 200px;
                                background-color: white;
                                box-shadow: 4px 5px 10px -5px black;

                                >.notneeded {
                                    cursor: no-drop;
                                }

                                >li {
                                    padding: 10px 0px 10px 10px;
                                    list-style: none;
                                    text-align: left;
                                    transition: all 0.3s ease-in;
                                    border-bottom: 1px solid#73738c;
                                    font-size: 11px;
                                    font-weight: 500;

                                    >a {
                                        text-decoration: none;
                                        color: #73738c;
                                        transition: all 0.1s ease-in;
                                        font-size: 11px;
                                        font-weight: 500;
                                        font-family: 'Roboto', sans-serif;

                                        &:hover {
                                            color: black;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }

            //OTHERS
            >.item {
                text-align: center;
                margin-inline: 3%;
                letter-spacing: 1px;
                padding: 5px 0;
                transition: all 0.1s ease;
                border: none;
                font-family: 'Roboto', sans-serif;
                font-weight: 580;
                cursor: pointer;

                >.active {
                    color: #DC152A;
                }

                >a {
                    text-decoration: none;
                    color: black;
                    font-size: 13px;
                    transition: all 0.4s ease-in-out;

                    &:hover {
                        color: #DC152A;
                        padding: 15px;
                    }
                }
            }
        }






        // RESPONSIVE
        >.modal {
            >.modal-dialog {
                margin: 0;
                width: 430px;

                >.modal-content {
                    top: 40px;
                    position: fixed;
                    border-radius: 0;
                    height: 100vh;
                    width: 55vw;
                    background-color: #f3f3f3;
                    border: none;

                    >.modal-header {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        width: 86%;
                        margin: 0 auto;
                        padding-bottom: 16px;
                        border-bottom: 2px solid black;

                        >img {
                            width: 140px;
                            height: 66px;
                            margin-top: 17.5px;
                        }

                        svg {
                            color: black;
                            font-size: 25px;
                            font-weight: 800;
                        }

                        >.btn-close {
                            background: transparent;
                        }
                    }

                    >.modal-body {
                        width: 90%;
                        margin: 0 auto;
                        padding: 20px auto;

                        >.modal-line {
                            margin: 5px 0;
                            text-align: justify;
                            cursor: pointer;
                            color: #73738c;

                            &:hover {
                                >.sub-menu-1 {
                                    display: block;
                                }
                            }

                            >.active {
                                color: black;
                            }

                            >.servicestitle {
                                >.active {
                                    color: black;
                                }

                                >a {
                                    transition: all 0.2s ease-in;
                                    font-family: 'Roboto', sans-serif;
                                    font-size: 14px;
                                    color: #73738c !important;
                                    text-decoration: none;

                                    &:hover {
                                        color: black;
                                        font-size: 16px;
                                    }
                                }

                                svg {
                                    margin-bottom: 5px;
                                }
                            }

                            >a {
                                font-family: 'Roboto', sans-serif;
                                text-decoration: none;
                                color: #73738c;
                                font-size: 14px;
                                transition: all 0.2s ease-in;

                                &:hover {
                                    color: black;
                                    font-size: 16px;
                                }
                            }


                            >.sub-menu-1 {
                                display: none;

                                >ul {
                                    text-align: justify;
                                    padding: 5px 0;


                                    >li {
                                        list-style: none;


                                        >a {
                                            text-decoration: none;
                                            font-size: 15px;
                                            margin-top: 12px;
                                            transition: all 0.2s ease-in;
                                            color: #73738c;

                                            &:hover {
                                                color: #000;
                                                font-size: 16px;
                                            }
                                        }

                                        &:hover {
                                            >.sub-menu-2 {
                                                display: block;
                                            }
                                        }

                                        >.sub-menu-2 {
                                            display: none;

                                            >ul {
                                                >.notneeded {
                                                    cursor: no-drop !important;
                                                }

                                                >li {
                                                    list-style: none;
                                                    margin-top: 5px;
                                                    text-align: left;

                                                    >a {
                                                        font-size: 14px;
                                                        text-decoration: none;
                                                        color: #73738c;
                                                        transition: all 0.2s ease-in;

                                                        &:hover {
                                                            color: black;
                                                            font-size: 16px;
                                                            font-weight: 800;
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}


//Responsive
@media(max-width:1114px) {
    header {
        >.container-fluid {
            >.nav-logo {
                >h1 {
                    margin-top: 25px;
                }
            }
        }
    }

}


@media(max-width:1114px) {
    header {
        >.container-fluid {
            >.nav-logo {
                >h1 {
                    margin-top: 50px;
                }
            }
        }
    }
}

//Image
@media(max-width:987px) {
    header {
        >.container-fluid {
            >.nav-logo {
                >a {
                    >img {
                        position: absolute;
                        width: 230px;
                        height: 220px;
                        top: -20px;
                        left: 10px;
                    }

                }
            }
        }
    }
}

@media(max-width:480px) {
    header {
        >.container-fluid {
            >.nav-logo {
                >a {
                    >img {
                        position: absolute;
                        width: 190px;
                        height: 180px;
                        top: 10px;
                        left: 0px;
                    }

                }
            }
        }
    }
}

@media(min-width:995px) {
    .modal-backdrop {
        display: none !important;
    }

    body {
        overflow: auto !important;
    }
}

//Slider
@media(max-width:510px) {
    .modal {
        >.modal-dialog {
            >.modal-content {
                height: 100vh;
                width: 90vw !important;
                background-color: #f3f3f3;
                border: none;
            }
        }
    }
}

@media(max-width:480px) {
    .modal {
        >.modal-dialog {
            >.modal-content {
                top: 55px !important
            }
        }
    }
}

@media(max-width:280px) {
    .modal {
        >.modal-dialog {
            >.modal-content {
                top: 47px !important
            }
        }
    }
}

