.information{
    width: 100%;
    >.info{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding:30px 0;
        >.title{
            width: 70%;
            padding-left: 10px;
            border-left: 4px solid rgb(236, 236, 236); 
                >p{
                    font-size: 15px;
                    line-height: 26px;
                    text-align: justify;
                    font-family: 'Roboto', sans-serif;
                }
            }
        >.info_box{
            margin-top: 40px;
            display: flex;
            width: 70%;
            >.box1{
                display: flex;
                flex-direction: column;
                align-items: center;
                text-align: center;
                width: 350px;
                margin:0 30px;
                >.img{
                    display: none;
                }
                >.text{
                    font-weight: bold;
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    text-align: justify;
                    >h3{
                        font-size: 19px;
                        line-height: 28px;
                        font-weight: 600;
                        font-family: 'Raleway', sans-serif;
                    }
                    >p{
                        font-size: 14px;
                        line-height: 26px;
                        font-family: 'Roboto', sans-serif;
                        font-weight: 500;
                    }
                }
            }
        }
        >.association{
            margin-top: 40px;
            width: 70%;
            text-align: justify;
            >h3{
                font-size: 24px;
                line-height: 28px;
                font-family: 'Raleway', sans-serif;
                font-weight: 700;
            }
            >p{
                font-size: 15px;
                line-height: 26px;
                font-family: 'Roboto', sans-serif;
                font-weight: 500;
            }
        }
        >.info_list{
            width: 80%;
            display: flex;
            justify-content: flex-start;
            align-items:center;
            >.list{
                display: flex;
                margin:5px 20px;
                >p{
                    font-size: 13px;
                    line-height: 26px;
                    font-family: 'Roboto', sans-serif;
                  
                }
            }
        }
    }
}


@media only screen and (max-width: 1111px) {
    .information{
        >.info{
            >.info_box{
                display: flex;
                flex-direction: column;
                >.box1{
                    width:100%;
                }
                
            }
        }
    }
}

@media only screen and (max-width: 650px) {
    .information{
        >.info{
            >.info_list{
                >.close{
                    display: none;
                }
              
            }
                
            
        }
    }
}