.home {
    width: 100%;
    height: 80vh;
    display: flex;
    font-family: 'Raleway', sans-serif;
    padding-left: 5px;

    >main {
        z-index: -1;
        position: absolute;
        text-transform: uppercase;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        width: 99%;
        height: 80%;
        justify-content: center;

        >.size {
            width: 100%;
            text-align: center;
            height: 100%;

            >.section {
                height: 100%;
                width: 100%;
                margin-top: 0 !important;

                >.mySwiper {
                    width: 100%;
                    height: 100%;

                    >.swiper-wrapper {
                        >.swiper-slide-prev {
                            >h1 {
                                display: none;
                            }
                        }

                        >.swiper-slide-active {
                            >div {
                                >.top {
                                    animation: captionAnimation1 2s ease-in-out;

                                }

                                >.bottom {
                                    animation: captionAnimation2 4s ease-in-out;
                                }
                            }
                        }

                        >.swiper-slide {
                            text-align: center;
                            font-size: 18px;
                            background: #fff;
                            position: relative;
                            width: 100%;
                            display: flex;
                            align-items: center;
                            z-index: -1;
                            height: 100%;

                            >img {
                                display: block;
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                                position: absolute;
                                z-index: -1;
                            }

                            >div {
                                width: 75%;
                                height: -15%;
                                text-align: right;

                                >.top {
                                    font-size: 55px;
                                    line-height: 68px;
                                    color: white;
                                    font-family: 'Raleway', sans-serif;
                                    font-weight: 750;
                                    padding-right: 12%;
                                }

                                >.bottom {
                                    font-size: 45px;
                                    color: #31096e;
                                    font-family: 'Raleway', sans-serif;
                                    font-weight: 700;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@keyframes captionAnimation1 {
    from {

        opacity: 0;
        transform: translateX(500px);
    }

    to {

        opacity: 1;
    }

}

@keyframes captionAnimation2 {
    from {

        opacity: 0;
        transform: translateY(500px);
    }

    to {

        opacity: 1;
    }

}


.section1 {
    display: flex;
    text-align: center;
    justify-content: center;
    width: 100%;
    padding-top: 50px;

    >div {
        >h3 {
            font-family: 'Roboto', sans-serif;
        }

        >div {
            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;

            >.box1 {
                display: flex;
                flex-direction: column;
                align-items: center;
                text-align: center;
                width: 350px;
                margin: 5px;

                >.img {
                    width: 30%;
                    margin: 20px 0;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    >svg {
                        font-size: 80px;
                    }
                }

                >.text {
                    font-weight: bold;
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    text-align: center;

                    >h3 {
                        font-size: 20px;
                        line-height: 28px;
                        text-align: center;
                        font-family: 'Raleway', sans-serif;
                        font-weight: 600;
                    }

                    >p {
                        font-size: 15px;
                        line-height: 26px;
                        text-align: center;
                        font-family: 'Roboto', sans-serif;
                        font-weight: 500;
                    }
                }
            }
        }
    }
}



.section2 {
    text-align: center;
    display: flex;
    justify-content: center;
    width: 100%;
    margin: 80px 0;

    &::before {
        content: "";
        width: 95%;
        height: 75%;
        position: absolute;
        background: no-repeat center/cover url("../Assets/background.webp");
        z-index: -1;
        filter: blur(2px);
        opacity: 0.6;
    }

    >.imagebox {
        width: 50%;

        >img {
            width: 100%;
            object-fit: cover;
        }
    }

    >.textbox {
        width: 50%;

        >div {
            width: 100% !important;
            height: 100%;

            >h3 {
                font-family: 'Raleway', sans-serif;
                font-weight: 900;
                height: 20%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-bottom: 0 !important;
            }

            >div {
                display: flex;
                width: 100%;
                justify-content: center;
                align-items: center;
                height: 80%;
                padding: 0 20px;


                >.swiper {
                    height: 100%;

                    >.swiper-wrapper {
                        >.Swiperslide {
                            >.box1 {
                                display: flex;
                                flex-direction: column;
                                align-items: center;
                                text-align: center;
                                width: 100%;
                                height: 100%;
                                cursor: grab;


                                >.img {

                                    width: 100%;
                                    height: 15%;
                                    margin-bottom: 10px;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    height: 30%;

                                    >svg {
                                        font-size: 100px !important;
                                    }
                                }

                                >.text {
                                    height: 70%;
                                    font-weight: bold;
                                    width: 100%;
                                    display: flex;
                                    flex-direction: column;
                                    text-align: center;
                              

                                    >h3 {
                                        font-size: 20px;
                                        line-height: 28px;
                                        text-align: center;
                                        font-family: 'Raleway', sans-serif;
                                        font-weight: 600;
                                        height: 20%;
                                    }

                                    >p {
                                        font-size: 15px;
                                        line-height: 26px;
                                        text-align: center;
                                        font-family: 'Roboto', sans-serif;
                                        font-weight: 500;
                                        height: 80%;
                                     

                                    }
                                }
                            }

                        }

                    }

                }
            }
        }

    }


}

.section3 {
    background-color: white;
    overflow-x: hidden;
    display: flex;
    justify-content: center;
    padding-top: 80px;

    >.middle {
        width: 95%;

        >.row {
            width: 100%;
            padding: 0 5px;
            margin: 0 !important;

            >.imgbox {
                padding: 0 !important;

                >img {
                    width: 100%;
                    height: 100%;
                }
            }

            >.text {
                padding: 25px 0px 0px 15px !important;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                text-align: justify;
                background-color: rgb(248, 246, 246);

                >h1 {
                    font-size: 20px;
                    line-height: 28px;
                    text-align: center;
                    font-family: 'Raleway', sans-serif;
                    font-weight: 600;
                }

                >p {
                    padding: 2% 5%;
                    font-size: 15px;
                    line-height: 26px;
                    text-align: center;
                    font-family: 'Roboto', sans-serif;
                    font-weight: 500;
                }
            }
        }

    }

}

.section4 {
    width: 100%;
    background-color: white;
    padding: 20px 0;
    margin-bottom: 40px;
    padding-bottom: 30px;
    overflow-x: hidden;


    >h1 {
        font-size: 30px;
        line-height: 26px;
        text-align: center;
        font-family: 'Raleway', sans-serif;
        font-weight: 900;
        margin: 40px 0;
    }

    >.row {
        width: 100%;
        padding: 0 5px;
        margin: 0 !important;

        >.col {
            padding: 0 !important;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: justify;


            >.smallsize {
                width: 80%;

                >.rowbox {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    width: 100%;
                    height: 100px;
                    justify-content: center;
                    padding-top: 30px;

                    >svg {
                        font-size: 50px;
                        transition: all 0.3s ease-out;
                        color: #1b78ac;

                        &:hover {
                            font-size: 60px;
                            color: rgb(76, 92, 180);
                        }
                    }

                    >h1 {
                        font-size: 23px;
                        line-height: 28px;
                        text-align: center;
                        margin-top: 10px;
                        cursor: default;
                        font-family: 'Raleway', sans-serif;
                        font-weight: 600;
                    }
                }

                >p {
                    padding: 2% 10%;
                    font-size: 14px;
                    line-height: 22px;
                    text-align: justify;
                    font-family: 'Roboto', sans-serif;
                    font-weight: 500;
                }
            }

        }

    }
}

.section5 {
    width: 100%;
    height: 40vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: none;

    >.back-video {
        position: absolute;
        right: 0;
        bottom: 0;
        z-index: -1;
    }

    >.content {
        text-align: center;

        >h1 {
            font-size: 45px;
            color: white;
            font-weight: 600;
            font-family: 'Roboto', sans-serif;
        }

        >p {
            font-size: 15px;
            line-height: 26px;
            text-align: center;
            font-family: 'Roboto', sans-serif;
            padding: 0 50px;
            color: white;
        }

        >button {
            text-decoration: none;
            display: inline-block;
            color: white;
            font-size: 15px;
            border: none;
            padding: 18px 70px;
            background-color: rgb(69, 143, 234);
            margin-top: 20px;
            transition: all 0.2s ease-in;
            font-family: 'Roboto', sans-serif;

            &:hover {
                background-color: white;
                color: rgb(147, 144, 144);
            }
        }
    }
}





@media only screen and (max-width: 938px) {
    .box2 {
        display: flex !important;
        flex-direction: column !important;

        >.divbox {
            width: 100% !important;
            text-align: center !important;
        }
    }

    .row {
        flex-direction: column;
    }

    .section1 {
        >div {
            >div {
                display: flex !important;
                flex-direction: column !important;

                width: 100%;

                >.box1 {
                    width: none;
                }
            }
        }
    }

    .section2 {
        display: flex;
        flex-direction: column;
        background: no-repeat center/cover url("../Assets/background.webp");
        z-index: -1;

        &::before {
            width: 0%;
            height: 0%;
        }

        >.imagebox {
            width: 100%;

            >img {
                width: 100%;
                height: 400px;
                object-fit: cover;
            }
        }

        >.textbox {
            width: 100%;
            height: 60vh;


            >div {
                height: 100%;
                >h3 {
                    font-family: 'Raleway', sans-serif;
                    font-weight: 900;
                    height: 20%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-bottom: 0 !important;
                    margin-top: 30px;
                }

                >div {
                    display: flex;
                    width: 100%;
                    justify-content: center;
                    align-items: center;
                    padding: 0 20px;

                    >.mySwiper {
                        >.swiper-pagination {
                            bottom: 5px !important;
                        }

                        >.swiper-wrapper {
                            >.Swiperslide {
                                >.box1 {
                                    >.img {
                                        height: 20%;
                                        >svg {
                                            font-size: 70px !important;
                                        }
                                    }

                                    >.text {
                                        height: 80%;
                                        >h3 {
                                            height: 20%;
                                        }
                                        >p { 
                                            height: 40%;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .section3 {
        background-color: white;

        >.middle {
            width: 100%;

            >.row {
                >.imgbox {
                    >img {
                        display: none;

                    }
                }
            }
        }
    }

    .section4 {
        >.row {
            >.col {
                >.smallsize {
                    >.rowbox {
                        height: auto;
                    }
                }
            }
        }
    }
}



