.Contact{
    font-family: 'Roboto', sans-serif;
    >.contactbox{
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        >.BOX1{
            width: 55%;
            background-color: #faf8f7;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            padding: 10px;
            box-shadow: -3px 0 10px -5px black;
           
    
          >.form_div{
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            flex:start;
            
          }
    
            >h1{
                font-size: 30px;
                line-height: 24px;
                text-transform: capitalize;
                font-weight: 700;
            }
            >p{
                font-size: 15px;
                line-height: 24px;
            }
          
        }
        
        >.BOX2{
            width: 35%;
            display: flex;
            flex-direction: column;
            align-items: center;
            padding-top: 3rem;
            box-shadow: 5px 0px 10px -5px black;
    
            >h1{
                font-size: 28px;
            }
            >.BOX{
                text-align: center;
                margin: 10px;
                >h2{
                    font-size: 24px;
                    line-height: 24px;
                    font-weight: 700;
                }
                >p{
                    font-size: 15px;
                    line-height: 24px;
                    font-weight: 500;
                }
            }
            
        }

    }
    
}


// MEDIA QUERIES

@media only screen and (max-width: 910px) {
    .Contact{
        padding: 15px;
       
       
        >.contactbox{
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            >.BOX1{
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                box-shadow: 0px -3px 10px -5px black;
              
               
                
                >.form_div{
                    >.formInput{
                        width: 100%;
                    }
                }
            }
            
            >.BOX2{
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                box-shadow: 0px 3px 10px -5px black;
            }
        }
 
        
    }

}
